.navigation {
  width: 470px;
  margin-right: 30px;
  padding-top: 9px;
}

.navigation__title {
  font-family: $ff-roboto-condensed;
  font-style: normal;
  font-weight: 400;
  font-size: 15px;
  line-height: 16px;
  letter-spacing: 0.05em;
  text-transform: uppercase;
  color: $color-default-white;
  margin: 0;
  margin-bottom: 21px;
}

.navigation__list {
  list-style-type: none;
  padding: 0;
  margin: 0;
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  height: 150px;
}

.navigation__item {
  width: 249px;
  min-height: 20px;
}

.navigation__item:not(:nth-child(4)) {
  margin-bottom: 5px;
}

.navigation__link {
  font-family: $ff-ptmono;
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 20px;
  color: $color-transparent;
}

.navigation__link:hover, .navigation__link:focus {
  color: $color-deep-sky-blue;
}

.navigation__button {
  display: none;
}

@media (max-width: 1023px) and (min-width: 768px) {
  .navigation {
    width: 320px;
    margin-right: 0;
    padding-top: 0;
  }

  .navigation__list {
    height: 230px;
  }

  .navigation__item:not(:last-child) {
    margin-bottom: 5px;
  }
}
