.goods {
  width: 100%;
  min-height: 960px;
  background-color: $color-black-pearl;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.goods__container {
  width: 1920px;
  min-height: 100%;
  padding: 66px 350px 125px;
}

.goods__text {
  font-family: $ff-roboto-condensed;
  font-style: normal;
  font-weight: 400;
  font-size: 36px;
  line-height: 51px;
  color: $color-default-white;
  margin: 0;
  margin-bottom: 53px;
}

.goods__list {
  list-style-type: none;
  padding: 0;
  display: grid;
  grid-template-columns: 350px 350px 350px;
  column-gap: 85px;
}

@media (max-width: 1023px) and (min-width: 768px) {
  .goods {
    height: 1080px;
  }

  .goods__container {
    width: 770px;
    padding: 54px 50px 75px;
  }

  .goods__list {
    grid-template-columns: 670px;
    row-gap: 30px;
  }

  .goods__text {
    margin-bottom: 38px;
  }
}
